.app-header {
  -webkit-align-items: flex-end;
          align-items: flex-end;
  -webkit-justify-content: space-between;
          justify-content: space-between;

}

.app-header h1 {
  /* width: 150px;
  height: 150px; */
  font-size: 26px;
}

.app-header h2 {
  font-size: 1.2rem;
  color: grey;
}
.search-panel {
  margin: 1rem 0;
}

.search-panel .search-input {
  width: auto;
  -webkit-flex-grow: 1;
          flex-grow: 1;
  margin-right: 3px;
}


.app-list-item {
  font-size: 1.25rem;
}

i {
  font-family: fontawesome !important;
}

.app-list-item button {
  width: 35px;
  height: 35px;
  margin: 3px;
  font-size: 17px;
  border: none;
  cursor: pointer;
}
.app-list-item button i {
  font-family: fontawesome !important;
}
.app-list-item button:focus {
    box-shadow: none;
    outline: none;
}
.app-list-item .btn-star {
    color: #FFD700;
}
.app-list-item .btn-trash {
    color: red;
}
.app-list-item .fa-heart {
    width: 35px;
    height: 35px;
    text-align: center;
    line-height: 35px;
    font-size: 16px;
    color: red;
    transition: 0.3s all;
    -webkit-transform: translateX(30px);
            transform: translateX(30px);
    opacity: 0;
}

.app-list-item-label {
  display: block;
  line-height: 35px;
  cursor: pointer;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  transition: 0.5s all;
}

.app-list-item.like .fa-heart {
    opacity: 1;
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
}

.app-list-item.important .app-list-item-label {
  color: #FFD700;
}

.app-list-item.important .btn-star {
    color: #aeaeae;
}

.app-list {
    margin-top: 50px;
}
.app-list .list-group-item {
  padding: 20px 35px 10px 35px;
  margin-top: 10px;
  border-radius: 20px;
}

.bottom-panel {
  margin-top: 20px;
  padding: 0 0 50px 0;
}

.bottom-panel .new-post-label {
  width: auto;
  -webkit-flex-grow: 1;
          flex-grow: 1;
  margin-right: 3px;
}

.app {
  margin: 0 auto;
  max-width: 800px;
}

